import { defineComponent, ref } from "vue";
import { AxTable } from "@/base-ui/table/index";
import { useFormDataShow } from "@/hooks/useFormDataShow";
import { utcToTime } from "@/utils/formatTime";
import { usePermission } from "@/hooks/usePermision";
import { ElMessage } from "element-plus";
export default defineComponent({
  props: {
    contentFormConfig: {
      type: Object,
      required: true
    },
    pageName: {
      type: String,
      required: true
    },
    title: {
      type: String,
      default: ""
    },
    showBottom: {
      type: Boolean,
      default: true
    },
    searchData: {
      type: Object,
      default: () => ({})
    }
  },

  async setup(props, {
    emit
  }) {
    // 
    const isCreate = usePermission(props.pageName, "create");
    const isDelete = usePermission(props.pageName, "delete");
    const isUpdate = usePermission(props.pageName, "update");
    const isQuery = usePermission(props.pageName, "query"); // prop

    const otherProps = props.contentFormConfig?.formItems.filter(item => {
      if (item?.slotName === "top") {
        return false;
      }

      if (item?.slotName === "createAt") {
        return false;
      }

      if (item?.slotName === "updateAt") {
        return false;
      }

      if (item?.slotName === "edit") {
        return false;
      }

      if (item?.slotName === "bottom") {
        return false;
      }

      return !!item?.slotName;
    }); // table

    const [formData, updateForm, deleteBtn] = await useFormDataShow(props.pageName); // 

    const currPage = ref(1); // 

    const handleCurrentChange = val => {
      currPage.value = val; // searchformdata
      // 

      updateForm({ ...props.searchData,
        offset: (currPage.value - 1) * 10
      });
    }; // 


    const createInfoBtn = () => {
      emit("createInfoBtn");
    }; // 


    const editInfo = val => {
      // ("editinfo:", val, formData)
      emit("editInfo", val);
    }; // 


    const visibles = ref(Array(formData.value.list.length).fill(false));

    const delInfo = async (val, index) => {
      visibles.value[index] = false;
      await deleteBtn(val.id);
      await updateForm();
      ElMessage({
        message: "",
        type: "success"
      });
    };

    return {
      formData,
      updateForm,
      utcToTime,
      otherProps,
      currPage,
      handleCurrentChange,
      isCreate,
      isDelete,
      isUpdate,
      isQuery,
      editInfo,
      delInfo,
      visibles,
      createInfoBtn
    };
  },

  components: {
    AxTable
  }
});