import { defineComponent, ref, watch } from "vue";
import { allMatch } from "@/service/allMatchFn";
import { AxForm } from "@/base-ui/form";
import { ElMessage } from "element-plus";
export default defineComponent({
  props: {
    maskFormConfig: {
      type: Object,
      required: true
    },
    defaultInfo: {
      type: Object,
      default: () => ({})
    },
    pageName: {
      type: String,
      required: true
    },
    title: {
      type: String,
      required: true
    },
    otherInfo: {
      type: Object,
      default: () => ({})
    }
  },

  setup(props, {
    emit
  }) {
    // dialog
    const dialogFormVisible = ref(false); // dialog

    const maskData = ref({});
    const AxFormRef = ref();
    watch(() => props.defaultInfo, newVal => {
      // 
      for (const key of props.maskFormConfig.formItems) {
        maskData.value[key.field] = newVal[key.field];
      }

      if (AxFormRef.value) {
        AxFormRef.value.formData = { ...maskData.value
        };
      }
    }, {
      immediate: true
    });

    const confirmAdd = async () => {
      dialogFormVisible.value = false;

      if (Object.keys(props.defaultInfo).length) {
        // 
        const editInfo = { ...maskData.value,
          id: props.defaultInfo.id
        };
        if (props.otherInfo) editInfo.menuList = props.otherInfo.menuList;
        delete editInfo.password;
        const res = await allMatch[props.pageName].editDataInfo(editInfo);

        if (typeof res.data === "string") {
          ElMessage({
            message: res.data,
            type: "warning"
          });
        } // 


        emit("updateTableData");
      } else {
        const res = await allMatch[props.pageName].createDataInfo(maskData.value);

        if (typeof res.data === "string") {
          ElMessage({
            message: res.data,
            type: "warning"
          });
        } // 


        emit("updateTableData");
      }
    };

    return {
      dialogFormVisible,
      maskData,
      AxFormRef,
      confirmAdd,
      allMatch
    };
  },

  components: {
    AxForm
  }
});