import { defineComponent, ref } from "vue";
export default defineComponent({
  props: {
    formItems: {
      type: Array,
      required: true
    },
    showIndex: {
      type: Boolean,
      default: false
    },
    showCheckBox: {
      type: Boolean,
      default: false
    },
    formData: {
      type: Object,
      required: true
    },
    showTree: {
      type: Boolean,
      default: false
    }
  },

  setup(prop) {
    const isTree = ref(false);

    if (prop.showTree) {
      isTree.value = {
        "row-key": "id",
        "tree-props": {
          children: "children"
        }
      };
    }

    return {
      isTree
    };
  }

});