import { defineComponent, ref } from "vue";
import { AxForm } from "@/base-ui/form/index";
export default defineComponent({
  props: {
    searchFormConfig: {
      type: Object,
      required: true
    }
  },

  setup(props, {
    emit
  }) {
    // 
    const formItems = props.searchFormConfig?.formItems || [];
    const formDataOrigin = {};

    for (const key of formItems) {
      formDataOrigin[key.field] = "";
    } // 


    const formData = ref(formDataOrigin); // AxForm

    const axForm = ref(); // 

    const handleReset = () => {
      if (axForm.value) {
        for (const key of formItems) {
          axForm.value.formData[key.field] = formDataOrigin[key.field];
        }
      }

      emit("resetBtn");
    }; // 


    const handleSearch = () => {
      // formdata, 
      emit("searchBtn", formData.value);
    };

    return {
      formData,
      handleSearch,
      handleReset,
      axForm
    };
  },

  components: {
    AxForm
  }
});