import { defineComponent, ref, watch } from "vue";
import { IInputType } from "../index";
export default defineComponent({
  props: {
    modelValue: {
      // form
      type: Object,
      required: true
    },
    formItems: {
      // form
      type: Array,
      default: () => []
    },
    labelWidth: {
      // el-formlabel
      type: String,
      default: "100px"
    },
    itemStyle: {
      // el-form-item
      type: Object,
      default: () => ({
        padding: "10px 40px"
      })
    },
    colLayout: {
      //
      type: Object,
      default: () => ({
        xl: 6,
        lg: 8,
        md: 12,
        sm: 24,
        xs: 24
      })
    }
  },

  setup(props, {
    emit
  }) {
    const formData = ref({ ...JSON.parse(JSON.stringify(props.modelValue))
    });
    watch(formData, newValue => {
      emit("update:modelValue", newValue);
    }, {
      deep: true,
      immediate: true
    });

    const optionsChanged = () => {
      ;
      "option changed";
    };

    return {
      IInputType,
      formData,
      optionsChanged
    };
  }

});